@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Girassol&display=swap);
*{margin:0;padding:0;box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box}.header{width:100%;height:100vh;background:url(/static/media/imgDeluxe.97a3d379.jpg) no-repeat center;background-size:cover}.header__navbar{width:100%;height:60px;padding:0 20px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;position:relative;z-index:300}.header__title{color:#fff;font-family:"Dancing Script", cursive;font-size:30px}.header__main{height:calc(100vh - 60px);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;position:relative;z-index:400}.header__name1{font-family:"Girassol", cursive;font-size:73px;color:#fff;letter-spacing:5pt}.header__name2{font-family:"Girassol", cursive;font-size:25px;color:#fff;font-weight:100}.header__drop{background-color:rgba(0,0,0,0.3);position:absolute;top:0;left:0;width:100%;height:100%}.meat-section{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:40px 0px}.meat-section__description{width:50%;padding:20px}.meat-section__slider{width:50%;padding:20px}.meat-section__title{text-align:center;font-size:33px;color:#0388a6}.meat-section__parrafo{color:gray;font-size:23px}.swiper-container{width:100%;height:100%}.swiper-slide{height:400px}.swiper-slide img{display:block;width:100%;height:100%;object-fit:cover}@media screen and (max-width: 768px){.meat-section{-webkit-flex-direction:column;flex-direction:column;padding:10px 0}.meat-section__description{width:100%;padding:10px 8px}.meat-section__title{font-size:23px}.meat-section__parrafo{color:gray;font-size:20px}.meat-section__slider{width:100%;padding:10px 8px}}

